
import {CardGroup} from 'reactstrap'
import ProjectCard from './card'

import mrhangy from './images/mrhangy.png'

function Group() {
    return (
        <CardGroup>
          <ProjectCard title="Pengen"
                       image="/portfolio/static/media/pengen.712de208.png"
                       subtitle='Drawing Canvas'
                       text='Created as an entry for The Mint Bean Hackathon November 2021.
                             It was created using Django Rest Framework and JavaScript'
                       url='https://0002pghg2i85392kkiugbg0aos7pot2qnle76s1034ochcvvh9hjkb8.siasky.net/'
          />
          <ProjectCard title="British Humour Explainer"
                       image="/portfolio/static/media/english_humour.d5fe8c63.png"
                       subtitle="Education"
                       text="A website where English language learners can learn about jokes in English. Created using Django"
                       url="https://englishhumour.herokuapp.com/"
          />

         <ProjectCard title="Mr Hangy"
                      image={mrhangy}
                      subtitle="Education"
                      text="A Hangman word game that I made for learners of English. Created using React"
                      url="https://moogwol.github.io/mr-hangy/"
          />
        </CardGroup>
    )
}

export default Group